import { motion } from 'framer-motion'
import React from 'react'


const AnimatedPages = ({children}) => {
  return (
    <motion.div
      className="animatable"
      initial={{
        opacity: 0,x:50,
      }}
      animate={{
        opacity:1,x:0,
      }}
      transition={{
        duration:0.25,
      }}
      exit={{
        opacity:0,x:-50
      }}
    >{children}
    </motion.div>
  )
}

export default AnimatedPages
